import { LoadingController } from '@ionic/angular';
import { DietService } from './../../services/diet.service';
import { Router } from '@angular/router';
import { UserService } from './../../services/user.service';
import { Component, OnInit } from '@angular/core';
import { PlansService } from 'src/app/services/plans.service';

@Component({
  selector: 'app-diet-breakfast',
  templateUrl: './diet-breakfast.page.html',
  styleUrls: ['./diet-breakfast.page.scss'],
})
export class DietBreakfastPage implements OnInit {
  mesAtual;
  breakfast; 
  programa;
  slideOpts = {
    initialSlide: 1,
    speed: 400
  };
  constructor(
    private userSrv: UserService,
    private planSrv: PlansService,
    private dietSrv: DietService,
    private router: Router,
    private loadCtrl: LoadingController
    ) { }

  ngOnInit() {
    this.loadDiet()
  }
  async loadDiet() {
    this.loadInfo();
    const loading = await this.loadCtrl.create({ message: "Carregando" });
  
    try {
      const monthPlan = await this.planSrv.getMonthPlan().toPromise();
      const dia = localStorage.getItem("dia");
      const dadosDieta = {
        mes: monthPlan.mes_atual,
        dia: dia,
        refeicao: 2,
      };
  
      const dietData = await this.dietSrv.getDietUserDay(dadosDieta).toPromise();
      
      loading.present();
      this.breakfast = dietData.dados;
      console.log(this.breakfast);
    } catch (error) {
      console.error("Erro ao carregar a dieta:", error);
    } finally {
      loading.dismiss();
    }
  }

  loadInfo() {
    this.planSrv.getMonthPlan().subscribe((data) => {
      console.log(data);
      this.mesAtual = data.mes_atual;
    });
    this.userSrv.getUserPlan().subscribe((response)=>{
      this.programa = response.programa
      console.log(this.programa)
    })
  }
  backToSnackWeek(){
    this.router.navigate(['snack-week'])
  }

}
